selected<script>
  import oiModal from '@/module/modal/comp/modal.vue'

  export default {
    name: 'ShowRule',

    components: { oiModal }
  }
</script>

<template>
  <span style="padding:0; margin:0">
    <button 
        type="button"     
        class="btn btn-xs"
        data-toggle="modal" 
        data-target="#rateDefectsWithinSLAShowRule">
        Regra
    </button>
    <oiModal id="rateDefectsWithinSLAShowRule" title="Regra">   
      <div style="margin:0; padding:10px" slot="body">
        <label class="fd-label">
          <!--
          Quantidade de defeitos que perderam SLA, divididos pela quantidade total de Defeitos X 100.<br>
          <br>
          -->
          So considerados os defeitos CLOSE e de CONSTRUO, das etapas de TI, UAT, TRG e DEV.<br>
          <br>
          No Clculo do SLA, so computados os tempos dos status IN_PROGESS, PENDENT (PROGRESS) e REOPEN.<br>
          <br>
          No grfico Temporal, os defeitos so contabilizadas no ms/ano em que eles estiveram os status considerados.<br>
          <br>
          So desconsideradas as Fb. de desenv. com o contedo: '', 'OI','LDER TCNICO', 'REA DE NEGCIOS', 'REA USURIA', 'AUTOMAO', 'ENGENHARIA', 'OI (API)' e 'OI (APLICATIVO)'.<br>
        </label>
      </div>
    </oiModal>
  </span>
</template>

<style scoped>
  .fd-label {
    font-weight: normal;
  }
</style>



