import oiMain from './Main.vue'
import oiViewModel from './comp/viewModel.vue'
import oiReport from './comp/report.vue'
// import oiViewModel2 from './comp/viewModel2.vue'

export default [
    { path: '/projectConsult', name: 'projectConsult', component: oiMain },
    { path: '/projectConsult/show/:subproject/:delivery', name: 'projectShow', component: oiViewModel },
    { path: '/projectConsult/report/:subproject/:delivery', name: 'projectReport', component: oiReport }
    // { path: '/projectConsult/report/:subproject/:delivery', name: 'projectReport', component: oiReport }
    // { path: '/project/report/:subproject/:delivery', name: 'projectReport', component: oiReport }
]



